<template>
    <div class="appBox">
        <ul>
            <li><a href="javascript:void(0);" class="ov"><span class="fl">头像</span><div class="fr ov"><img class="fl" @click="chooseAvatar" :src="avatar" alt=""><i class="fl"></i></div></a></li>
            <li><router-link to="/nickname" class="ov"><span class="fl">昵称</span><div class="fr ov"><p class="fl">{{name}}</p><i class="fl"></i></div></router-link></li>
        </ul>
        <input @change="uploadAvatar" type="file" name="file" accept="image/*" hidden id="upavatar">
    </div>
</template>

<script>
    import { uploadAvatar } from "@/api/user"
    import Toast from 'vant/lib/toast'
    import 'vant/lib/toast/style'
    import { mapGetters } from 'vuex'

    export default {
        name: "Userinfo",
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        data()
        {
            return {
                userAvatar:''
            }
        },
        mounted()
        {
            this.userAvatar = this.avatar
        },
        methods:{

            chooseAvatar()
            {
                let fileDom = document.getElementById('upavatar')
                fileDom.click()
            },
            uploadAvatar(e)
            {

                let file = e.target.files[0]
                /* eslint-disable no-undef */
                let param = new FormData() // 创建form对象
                param.append('file', file, file.name) // 通过a
                uploadAvatar(param).then(res => {

                    this.$store.dispatch('user/setAvatar', res.imgUrl).then(() => {

                    }).catch(() => {

                    })
                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/userinfo.css";
</style>



